.Buttons {
  padding: 0px; }

.Buttons__Button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-right: 5px;
  margin-top: 10px;
  vertical-align: top; }

.Buttons__Select {
  display: inline-block;
  margin-right: 5px;
  margin-top: 10px;
  width: 150px;
  vertical-align: top; }

@media all and (max-width: 480px) {
  .Buttons__Button {
    margin-right: 0;
    padding-right: 0;
    width: 50%; }
  .Buttons__Button:nth-child(2n) {
    padding-right: 5px; }
  .Buttons__Select {
    width: 100%; } }



