.bar {
  position: relative;
  background-color: #6f737c; }

.bar__progress {
  background-color: #a6bc7c;
  height: 100%;
  position: absolute;
  overflow: visible;
  -webkit-transition: width 0.3s ease-out;
  -o-transition: width 0.3s ease-out;
  transition: width 0.3s ease-out; }

.bar__text {
  color: #ffffff;
  padding: 5px 0;
  position: relative;
  text-align: center;
  width: 100%; }

.bar__progress--overloaded {
  background-color: #cc230d;
  -webkit-animation: overload 0.4s ease-out;
          animation: overload 0.4s ease-out; }

@-webkit-keyframes overload {
  0% {
    background-color: #a6bc7c; }
  90% {
    background-color: #fe6d5a; }
  100% {
    background-color: #cc230d; } }

@keyframes overload {
  0% {
    background-color: #a6bc7c; }
  90% {
    background-color: #fe6d5a; }
  100% {
    background-color: #cc230d; } }



