@import url('https://fonts.googleapis.com/css?family=Roboto:100,300');

.App {
  background: #838790;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 100;
  height: 100%;
  letter-spacing: 1px;
  text-align: center;
  padding-top: 40px;
}

.App__Title {
  color: white;
  margin-bottom: 20px;
}

.App__Bars {
  background-color: #666a72;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 680px;
  padding: 60px 80px;
  margin: auto;
  min-height: 300px;
  text-align: left;

}

.App__Loader {
  background-color: #666a72;
  color: white;
  max-width: 680px;
  margin: auto;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 300px;

}


@media all and (max-width: 480px) { 
    .App {
      padding-top: 0px;
    }
    .App__Bars, .App__Loader {
      padding: 20px;
      width: 100%;
      height: 100%;

    }
}




